*, *:after, *:before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 13px;
  background: #EEF2F4;
}

.container {
  margin: 0 120px;
  max-width: 85rem;
  padding: 0 10px;
}

/* TOP BAR */
.logo {
  width: 87px;
  display: flex;
  fill: white;
  gap: 2px;
}

/* svg#logo-jiji {
  margin-right: 5px;
} */
.topbar {
  background: #3DB83A;
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  height: 56px;
  padding: 0 120px;
  border-bottom: 2px solid #3c9f19;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;
}

.tagline {
  font-size: 16px;
  color: #fff;
  opacity: 0.6;
}

.topbar-cta {
  display: flex;
}

.topbar-cta {
  display: flex;
  color: #fff;
  word-spacing: 2px;
  align-items: center;
}

.topbar-cta button {
  margin: 0 15px;
  background: #ffa010;
  color: #fff;
  height: 40px;
  padding: 0 8px;
  width: 120px;
  border: 0;
  border-radius: 5px;
}

/* BANNER */
.banner {
  height: 351px;
  background: linear-gradient(160.29deg, #3db83a .67%, #378705 100.93%);
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

svg#geo-marker {
  width: 11px;
  height: 11px;
  fill: #fff;
  margin-right: 2px;
}

.search-pane {
  width: 40%;
}

.search-pane {
  width: 40%;
  text-align: center;
  font-size: 18px;
  color: #fff;
}

.search-pane span {
  font-size: 14px;
  background: rgba(48, 58, 75, .7);
  padding: 5px 8px;
  border-radius: 4px;
}

.search-box {
  width: 600px;
  height: 56px;
  display: flex;
  margin-top: 60px;
  background: #fff;
  border-radius: 3px;
}

.search-box input {
  padding: 18px 72px 18px 16px;
  color: #35495e;
  border-color: transparent;
  width: 544px;
  border-radius: 3px;
}

.search-box input:focus-visible {
  outline: none;
}

.search-icon {
  width: 56px;
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
}

svg#search {
  width: 24px;
  height: 24px;
  fill: rgb(61, 184, 58);
}

.search-box input::placeholder {
  color: #35495e;
  font-size: 14px;
  opacity: .6;
}

img.left-image {
  position: absolute;
  bottom: 0;
  left: 180px;
}

img.right-image {
  position: absolute;
  bottom: 0;
  right: 30px;
}

/* SIDEBAR */
.sidebar {
  flex-basis: 25%;
  height: 585px;
  box-shadow: 1px 1px 4px rgb(80 114 125 / 40%);
  background: #fff;
  position: sticky;
  top: 70px;
}

svg#arrow-right {
  width: 10px;
  height: 10px;
}

.categories li {
  display: flex;
  align-items: center;
  grid-gap: 15px;
  gap: 15px;
  padding: 7px 10px;
}

.categories li span {
  margin-left: auto;
}

.categories {
  padding: 15px;
}

.categories li img {
  width: 32px;
  height: 32px;
}

.cat-info {
  font-size: 15px;
}

.cat-info div {
  font-size: 13px;
}

/* PAGE CONTENT */
.page-content {
  display: flex;
  gap: 15px;
}

/* MAIN CONTENT */
.main-content {
  flex-basis: 75%;
}

/* SLIDER */
.slider {
  margin-bottom: 20px;
  display: flex;
  gap: 15px;
}

.slider__image {
  background: url(https://assets.jiji.ng/static/img/banners/main_how_to_buy.jpg);
  background-size: cover;
  flex-basis: 75%;
}

.slider__ad {
  flex-basis: 25%;
  background: #FFA010;
  color: #fff;
  padding: 16px;
  text-align: center;
}

.slider__ad p {
  font-size: 18px;
}

.slider__ad-icon {
  height: 95px;
  width: 95px;
  background: #fff;
  margin: 15px auto;
  box-shadow: 1px 1px 4px rgb(80 114 125 / 40%);
  border-radius: 50%;
  position: relative;
}

.slider__ad-icon::after {
  width: 7%;
  height: 60%;
}

.slider__ad-icon::before {
  width: 60%;
  height: 7%;
}

.slider__ad-icon::after, .slider__ad-icon::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  border-radius: 4px;
  background: #ffa010;
}

.slider__ad h3 {
  font-size: 20px;
  font-weight: 700;
}

.slider__ad p {
  font-size: 18px;
}

/* TRENDING ADS */
.trending-ads {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
}

.trending-ads {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
  grid-gap: 15px;
  gap: 15px;
  justify-content: space-between;
}

.ad {
  width: 23%;
}

.ad__info {
  font-size: 14px;
  padding: 24px 8px 8px;
  margin-top: -3px;
  position: relative;
}

.ad__price {
  color: #3db83a;
}

.ad__img {
  width: 100%;
  height: 159px;
  object-fit: cover;
}

.ad__card {
  background: #fff;
  border-radius: 4px;
}

svg#tab-saved-outlined {
  width: 20px;
  height: 20px;
  fill: rgb(61, 184, 58);
}

.ad__info button {
  background: #fff;
  height: 36px;
  width: 36px;
  border: none;
  border-radius: 50%;
  position: absolute;
  right: 12px;
  top: -18px;
}

.ad__img-counter {
  background: rgba(0, 0, 0, .5);
  color: #fff;
  font-size: 10px;
  border-radius: 0 8px 0 0;
  position: absolute;
  top: -15px;
  left: 0;
  width: 15px;
  height: 15px;
  text-align: center;
  padding: 2px;
}

/* FOOTER */
.footer__above {
  text-align: center;
  margin-bottom: -3px;
}

.footer__copyright {
  color: #279625;
  padding: 25px 0;
  text-align: center;
}

.footer__main {
  background: #3db83a;
}

.footer__menu-item-wrapper {
  color: #279625;
}

.footer__container {
  display: flex;
  padding-top: 40px;
}

.footer__container>* {
  flex-basis: 25%;
}

.footer__menu-item-wrapper ul {
  list-style: none;
}

.footer__menu-item-wrapper a {
  color: #fff;
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
}

.footer__menu-item-wrapper li {
  margin-top: 16px;
}

.footer__menu-item-wrapper h4 {
  font-size: 14px;
}